<template>

  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
            <router-link class="breadcrumbs__link" :to="{name: 'main'}">
                Каталог
            </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Корзина
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
        <div v-if="cartLoading">Загрузка товаров...</div>
        <div v-if="cartLoadingFailed">Произошла ошибка при загрузке товаров
        <button @click.prevent="loadCart()">Попробовать еще раз</button>
        </div>
      <span class="content__info">
        {{ products.length + " " + getWord}}
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <ul class="cart__list">
            <!-- при иттерации обращаемся к геттеру!!! -->
            <CartItem v-for="item in products" :key="item.productId" :item="item"></CartItem>
          </ul> 
        </div>

        <div class="cart__block">
          <p class="cart__desc">
            Мы&nbsp;посчитаем стоимость доставки на&nbsp;следующем этапе
          </p>
          <p class="cart__price">
            Итого: <span>{{ totalPrice | numberFormat }} ₽</span>
          </p>

          <router-link v-show="products.length > 0" tag="button" :to="{name: 'order'}" class="cart__button button button--primery" type="submit">
            Оформить заказ
          </router-link>
        </div>
      </form>
    </section>
  </main>

</template>
<script>
    // импорт карточки товара корзины
    import CartItem from '@/components/CartItem.vue';
    // импорт функции получения слова
    import getWord from '@/helpers/getWord';
    // импорт функции формата цены
    import numberFormat from '@/helpers/numberFormat';
    // импорт метода для подгрузки геттеров
    import { mapGetters } from 'vuex';
    // вызов метода и мутации из глобального хранилища
    import { mapState, mapActions } from 'vuex';


    export default {
      // data() {
      //   return {
      //   // свойство состояния прелоадера
      //   cartLoading: false,
      //   // свойство состояния ошибки загрузки
      //   cartLoadingFailed: false,
      //   }
      // },
    comments: { CartItem },
    filters: { numberFormat },
    computed: {
        ...mapGetters({ products: "cartDetalProducts", totalPrice: "cartTotalPrice" }),
        ...mapState({cartLoading: 'cartLoading', cartLoadingFailed: 'cartLoadingFailed'}),
        // получение слова для количества товаров в корзине
        getWord
        // при иттерации обращаемся к геттеру
        // products() {
        //   return this.$store.getters.cartDetalProducts;
        // }
    },
    components: { CartItem },
    methods: {
    // подмешиваем действия, перечисленные в масиве
    ...mapActions(['loadCart']),
  },
  // created() {
  //   this.loadCart()
  //   }
}
</script>