// импорт модуля Vue
import Vue from "vue";
// импорт модуля Vuex
import Vuex from "vuex";
// импорт справочника с товарами
// import products from "@/data/products";
// импорт базового пути
import {API_BASE_URL} from '../config';
import axios from 'axios';
import { Promise } from "core-js";

// сообщаем vue об использовании vuex
Vue.use(Vuex);
// создаем и экспортируем новое хранилище
export default new Vuex.Store({
    // свойство для хранения списка товаров в корзине
    state: {
        // товары в корзине
        cartProducts: [],
        // ключ пользователя для работы с АПИ
        userAccessKey: null,
        // информация о товарах в корзине
        cartProductsData: [],

        orderInfo: null,

        cartLoading: false,
        cartLoadingFailed: false,

    },
    // для изменения состояния vuex используем мутации
    mutations: {
        // addProductToCart(state, {productId, amount}) {
        //     // проверка на наличие товара в корзине
        //     const item = state.cartProducts.find(item => item.productId === productId);
        //     // если такой товар уже в корзине то добавляем только число
        //     if(item) {
        //         item.amount += amount;
        //         // если такой товар в корзине отсутствует, добавляем его в корзину
        //     } else {
        //         state.cartProducts.push({
        //         productId,
        //         amount,
        //         })
        //     } 
        // },
        updateOrderInfo(state, orderInfo) {
            state.orderInfo = orderInfo;
        },

        resetCart(state) {
            state.cartProducts = [],
            state.cartProductsData = []
        },

        updateCartProductAmount(state, {productId, amount}) {
            // проверка на наличие товара в корзине
            const item = state.cartProducts.find(item => item.productId === productId);
            // если такой товар уже в корзине то меняем только число
            if(item) {
                item.amount = amount;
            }
        },
        deleteCartProduct(state, productId) {
            state.cartProducts = state.cartProducts.filter(item => item.productId !== productId);
        },
        // мутаци изменеия свойства userAccessKey
        updateUserAccesKey(state, accessKey) {
            state.userAccessKey = accessKey;
        },
        // мутация изменеия свойства cartProductsData
        updateCartProductsData(state, items){
            state.cartProductsData = items;
        },
        // мутация синхронизации данных исходны в cartProducts и приходящих из АПИ cartProductsData
        syncCartProducts(state) {
            state.cartProducts = state.cartProductsData.map(item => {
                return {
                    productId: item.product.id,
                    amount: item.quantity
                    // ...item,
                    // product: products.find(p => p.id === item.productId)
                }
            })
        },
        },
       
    // геттер - функция, которая вычисляет значение и кэширует результат
    getters: {
        // геттер для получения детальной информации в корзине
        cartDetalProducts(state){
            // получам массив с товарами
            return state.cartProducts.map(item => {
                const product = state.cartProductsData.find(p => p.product.id === item.productId).product;
                return {
                    ...item,
                    product: {
                        ...product,
                            image: product.image.file.url
                    }
                }
            });
        },
        // геттер получения итоговой цены
        cartTotalPrice(state, getters) {
            return getters.cartDetalProducts.reduce((acc, item) => (item.product.price * item.amount) + acc, 0)
        }
    },
    actions: {

        loadOrderInfo(context, orderId) {
            return axios
                    .get(API_BASE_URL + '/api/orders/' + orderId, {
                    params: {
                    userAccessKey: context.state.userAccessKey  
                    }
            })
            .then(response => {
                context.commit('updateOrderInfo', response.data)
            });
        },

        // действие для получения информации о корзине с ключем пользователя
        loadCart(context){

            context.state.cartLoadingFailed = false;
            context.state.cartLoading = true;
            clearTimeout(this.loadCartTimer);
            this.loadCartTimer = setTimeout (() => {
                return axios
                    .get(API_BASE_URL + '/api/baskets', {
                    params: {
                    userAccessKey: context.state.userAccessKey  
                    }
                })
                .then(response => {
                    if(!context.state.userAccessKey) {
                        localStorage.setItem('userAccessKey', response.data.user.accessKey);
                        context.commit('updateUserAccesKey', response.data.user.accessKey);
                    }
                        context.commit('updateCartProductsData', response.data.items);
                        context.commit('syncCartProducts');
                    })
                // ловим ошибку
                .catch(() => context.state.cartLoadingFailed = true)
                // отключаем прелоадер
                .then(() => context.state.cartLoading = false)
            }, 3000);
        },

        //действие для добавление товара в корзину
        addProductToCart(context, {productId, amount}){
                return (new Promise(resolve => setTimeout(resolve, 2000)))
                .then(() => {
                    return axios
                .post(API_BASE_URL + '/api/baskets/products', {
                    productId: productId,
                    quantity: amount
                }, {
                    params: {
                        userAccessKey: context.state.userAccessKey
                    }
                })
                .then(response => {
                    context.commit('updateCartProductsData', response.data.items);
                    context.commit('syncCartProducts');
                })
                })
        },
        // действие изменения количества товара в корзине
        updateCartProductAmount(context, {productId, amount}) {
            context.commit('updateCartProductAmount', {productId, amount});

            if (amount < 1) {
                return;
            }
            return axios
            .put(API_BASE_URL + '/api/baskets/products', {
                productId: productId,
                quantity: amount
            }, {
                params: {
                    userAccessKey: context.state.userAccessKey
                }
            })
            .then(response => {
                context.commit('updateCartProductsData', response.data.items);
            })
            .catch(() => {
                context.commit('syncCartProducts');
            })
        },

        // действие удаления товара в корзине
        deleteCartProduct(context, productId) {
            context.commit('deleteCartProduct', productId)
            return axios
            .delete(API_BASE_URL + '/api/baskets/products', {
                data: {productId: productId},
                params: { userAccessKey: context.state.userAccessKey},
            })
            .then(response => {
                context.commit('updateCartProductsData', response.data.items);
            })
            .catch(() => {
                context.commit('syncCartProducts');
            })
        }
    }
});