export default function getWord() {
    let count = this.products.length % 100;
        if(count >=10 && count <=20) {
            return "товаров"
        } else {
            if (count === 1) {
                return "товар";
            }
             else if (count >= 2 && count <= 4) {
                return "товара";
            }
            else {
                return "товаров";
            }
        }     
    }
