// импорт модуля vue
import Vue from 'vue';
// импорт vue router
import VueRouter from 'vue-router';
// импорт страницы маин
import MainPage from "@/pages/MainPage";
// импорт не найденной страницы
import NotFoundPage from "@/pages/NotFoundPage";
// импорт страницы продукта
import ProductPage from "@/pages/ProductPage";
// импорт страницы корзины
import CartPage from "@/pages/CartPage";
// импорт страницы оформления товара
import OrderPage from "@/pages/OrderPage";
// импорт страницы успешно оформленного товара
import OrderInfoPage from "@/pages/OrderInfoPage";


// сообщаем vue что собираемся использовать vue router
Vue.use(VueRouter);

// создаем массив с маршрутами
const routes = [
    {name: 'main', component: MainPage, path: '/'},
    {name: 'product', component: ProductPage, path: '/product/:id'},
    {name: 'cart', component: CartPage, path: '/cart'},
    {name: 'order', component: OrderPage, path: '/order'},
    {name: 'orderInfo', component: OrderInfoPage, path: '/order/:id'},
    {name: 'notFound', component: NotFoundPage, path: '*'},
];

// создаем новый экземпляр роутера и подключаем к нему масив с маршрутами
const router = new VueRouter({
    routes
});
// экспортируем его
export default router;