<template>
  <li class="cart__item product">
    <div class="product__pic">
        <img :src="item.product.image" width="120" height="120" alt="item.product.title">
    </div>
    <h3 class="product__title">
        {{ item.product.title }}
    </h3>
    <p class="product__info">
        Объем: <span>128GB</span>
    </p>
    <span class="product__code">
        Артикул: {{ item.product.id }}
    </span>

    <div class="product__counter form__counter">
        <button type="button" aria-label="Убрать один товар" @click="deleteProd">
        <svg width="10" height="10" fill="currentColor">
            <use xlink:href="#icon-minus"></use>
        </svg>
        </button>

        <input type="text" v-model.number="amount" name="count">

        <button type="button" aria-label="Добавить один товар" @click="addProd">
        <svg width="10" height="10" fill="currentColor">
            <use xlink:href="#icon-plus"></use>
        </svg>
        </button>
    </div>

    <b class="product__price">
        {{ (item.amount * item.product.price) | numberFormat }} ₽
    </b>

    <button 
        class="product__del button-del" 
        type="button" aria-label="Удалить товар из корзины" 
        @click.prevent="deleteProduct(item.productId)">
        <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
        </svg>
    </button>
    </li>  
</template>

<script>
    // импорт метода для подгрузки мутаций
    // import { mapMutations } from 'vuex';
    import { mapActions } from 'vuex';
    // импорт фнкции фильтра цены
    import numberFormat from '@/helpers/numberFormat';
    // 
export default {
    // подключаем входящие параметры
    props: ['item'],
    // подключаем фильтры
    filters: { numberFormat },
    computed: {
        amount: {
           // возврат значения количесвта из входного параметра
            get() {
                return this.item.amount;
            },
            // вызов мутации
            set(value) {
                this.$store.dispatch('updateCartProductAmount', {productId: this.item.productId, amount: value})
            }
        }
    },
    methods: {
        ...mapActions({updateCartProductAmount: 'updateCartProductAmount', deleteProduct: 'deleteCartProduct'}),
        // ...mapMutations({deleteProduct: 'deleteCartProduct'}),
        // deleteProduct(productId) {
        //     // вызов мутации
        //     this.$store.commit('deleteCartProduct', productId);
        // }
        addProd() {
            if(this.amount < 10) {
                ++this.amount  
            } else {
               alert('На складе отсутствует необходимое количество выбранных Вами товаров') 
            }
            
        },
        deleteProd() {
            if(this.amount > 0) {
              --this.amount  
            }
        },
    }
}
</script>