<template>
    <main class="content container">
      <div class="content__top">
        <ul class="breadcrumbs">
          <li class="breadcrumbs__item">
            <a class="breadcrumbs__link" href="index.html">
              Каталог
            </a>
          </li>
          <li class="breadcrumbs__item">
            <a class="breadcrumbs__link" href="cart.html">
              Корзина
            </a>
          </li>
          <li class="breadcrumbs__item">
            <a class="breadcrumbs__link">
              Оформление заказа
            </a>
          </li>
        </ul>
  
        <h1 class="content__title">
          Корзина
        </h1>

        <span class="content__info">
          {{ products.length + " " + getWord }}
        </span>
      </div>

      <div v-if="orderLoading">Отправка запроса...</div>
      <div v-else-if="orrderLoadingFailed">Не удалось отправить запрос</div>
      <section v-else class="cart">
        <form class="cart__form form" action="#" method="POST" @submit.prevent="order">
          <div class="cart__field">
            <div class="cart__data">

              <BaseFormText v-model="formData.name" :error="formError.name" title="ФИО" placeholder="Введите ваше полное имя" />
              
              <BaseFormText v-model="formData.address" :error="formError.address" title="Адрес" placeholder="Введите ваш адрес" />

              <BaseFormText v-model="formData.phone" :error="formError.phone" type="tel" title="Телефон" placeholder="Введите ваш телефон" />

              <BaseFormText v-model="formData.email" :error="formError.email" type="email" title="email" placeholder="Введите ваш Email" />

              <!-- <label class="form__label">
                <input class="form__input" v-model="formData.address" type="text" name="address" placeholder="Введите ваш адрес">
                <span class="form__error" v-if="formError.address">{{formError.address}}</span>
              </label> -->
  
              <!-- <label class="form__label">
                <input class="form__input" v-model="formData.phone" type="tel" name="phone" placeholder="Введите ваш телефон">
                <span class="form__value">Телефон</span>
                <span class="form__error" v-if="formError.phone">{{formError.phone}}</span>
              </label> -->
  
              <!-- <label class="form__label">
                <input class="form__input" v-model="formData.email" type="email" name="email" placeholder="Введи ваш Email">
                <span class="form__error" v-if="formError.email">{{formError.email}}</span>
              </label> -->
              
              <BaseFormTextarea v-model="formData.comment" :error="formError.comment" title="Коментарий к заказу" placeholder="Ваши пожелания"></BaseFormTextarea>
            </div>
  
            <div class="cart__options">
              <h3 class="cart__title">Доставка</h3>
              <ul class="cart__options options">
                <li class="options__item">
                  <label class="options__label">
                    <input class="options__radio sr-only" type="radio" name="delivery" value="0" checked="">
                    <span class="options__value">
                      Самовывоз <b>бесплатно</b>
                    </span>
                  </label>
                </li>
                <li class="options__item">
                  <label class="options__label">
                    <input class="options__radio sr-only" type="radio" name="delivery" value="500">
                    <span class="options__value">
                      Курьером <b>500 ₽</b>
                    </span>
                  </label>
                </li>
              </ul>
  
              <h3 class="cart__title">Оплата</h3>
              <ul class="cart__options options">
                <li class="options__item">
                  <label class="options__label">
                    <input class="options__radio sr-only" type="radio" name="pay" value="card">
                    <span class="options__value">
                      Картой при получении
                    </span>
                  </label>
                </li>
                <li class="options__item">
                  <label class="options__label">
                    <input class="options__radio sr-only" type="radio" name="pay" value="cash">
                    <span class="options__value">
                      Наличными при получении
                    </span>
                  </label>
                </li>
              </ul>
            </div>
          </div>

        <OrderProductList></OrderProductList>
        
        <div class="cart__error form__error-block" v-if="formErrorMessage">
        <h4>Заявка не отправлена!</h4>
        <p>
           {{ formErrorMessage }}
        </p>
        </div>

          
          
        </form>
      </section>
    </main>
  </template>
  
  <script>

import BaseFormText from '@/components/BaseFormText.vue';
import BaseFormTextarea from '@/components/BaseFormTextarea.vue';
import OrderProductList from '@/components/OrderProductList.vue';
import { API_BASE_URL } from '@/config';
import { mapGetters } from 'vuex';
import axios from 'axios';
import getWord from '@/helpers/getWord';
    export default {

        components: { BaseFormText, BaseFormTextarea, OrderProductList },



        data() {
            return {
                formData: {},
                formError: {},
                formErrorMessage: '',
                orderLoading: false,
                orrderLoadingFailed: false,

            }
        },
        methods: {
          order() {
              this.orderLoading = true;
              this.orrderLoadingFailed = false;
              this.formError = {};
              this.formErrorMessage = '';

              axios
                  .post(API_BASE_URL + '/api/orders', {
                    ...this.formData
                  }, {
                    params: {
                      userAccessKey: this.$store.state.userAccessKey
                    }
                  })

                  .then(response => {
                    this.$store.commit('resetCart')
                    this.$store.commit('updateOrderInfo', response.data)
                    this.$router.push({name: 'orderInfo', params: {id: response.data.id}})
                    this.orderLoading = false
                  })
                    
                  .catch(error => {
                    this.formError = error.response.data.error.request || {};
                    this.formErrorMessage = error.response.data.error.message;
                    this.orderLoading = false
                  })
          }
        },

        computed: {
          ...mapGetters({ products: "cartDetalProducts" }),
          getWord,
        },
    }
  </script>