<template>
    <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">
        Каталог
      </h1>
      <span class="content__info">
        152 товара
      </span>
    </div>

    <div class="content__catalog">

      <ProductFilter 
        v-bind:price-from.sync="filterPriceFrom"
        v-bind:price-to.sync="filterPriceTo"
        v-bind:category-id.sync="filterCategoryId"
        v-bind:color-value.sync="filterColorValue">
      </ProductFilter>
      
      <section class="catalog">

        <div v-if="productsLoading">Загрузка товаров...</div>
        <div v-if="productsLoadingFailed">Произошла ошибка при загрузке товаров
        <button @click.prevent="loadProducts">Попробовать еще раз</button>
        </div>
<!-- @gotoPage="(pageName, pageParams) => $emit('gotoPage', pageName, pageParams)" -->
        <ProductList 
            v-bind:products="products" 
        ></ProductList>

        <BasePagination v-model="page" v-bind:count="countProducts" v-bind:per-page="productsPerPage"></BasePagination>

  
      </section>
      
    </div>
  </main>
</template>

<script>
// импорт базового пути
import {API_BASE_URL} from '../config'
// import products from '@/data/products.js'; при работе с API не требуется
import ProductList from '@/components/ProductList.vue';
import BasePagination from '@/components/BasePagination.vue';
import ProductFilter from '@/components/ProductFilter.vue';
// import colors from '@/data/colors';
import axios from 'axios';

export default {
    components: { ProductList, BasePagination, ProductFilter },
    data() {
      return {
        filterPriceFrom: 0,
        filterPriceTo: 0,
        filterCategoryId: 0,
        filterColorValue: 0,
        
        page: 1,
        productsPerPage: 3,
        // свойство состояния которое хранит ответ от сервера
        productsData: null,

        // свойство состояния прелоадера
        productsLoading: false,
        // свойство состояния ошибки загрузки
        productsLoadingFailed: false,
      }
    },
    computed: {
                    // код для работы с данными от локального источника
                    // возврат отфильтрованного фильтра товаров
                    // filteredProducts() {
                    //   let filteredProducts = products;
                    //   // логика фильтрации
                    //   if(this.filterPriceFrom > 0) {
                    //     filteredProducts = filteredProducts.filter(product => product.price > this.filterPriceFrom);
                    //   }

                    //   if(this.filterPriceTo > 0) {
                    //     filteredProducts = filteredProducts.filter(product => product.price < this.filterPriceTo);
                    //   }

                    //   if(this.filterCategoryId) {
                    //     filteredProducts = filteredProducts.filter(product => product.categoryId === this.filterCategoryId);
                    //   }

                    //   if(this.filterColorValue) {
                    //     filteredProducts = filteredProducts.filter(product => product.colors.includes(this.filterColorValue));
                    //   }

                    //   return filteredProducts

                    // },
      // логика для отображения количества товаров на странице
      products() {
                    // код для работы с данными от локального источника
                    // // первый индекс для метода slice
                    // const offset = (this.page - 1) * this.productsPerPage;
                    // // возврат количества товаров на странице
                    // return this.filteredProducts.slice(offset, offset + this.productsPerPage);
        // код для работы с сервером
        // берем товары из состояния
        return this.productsData 
          ? this.productsData.items.map(product => {
            return {
              ...product,
                image: product.image.file.url,   
            }
          }) 
          : [];
      },
      // возврат общего количества товаров
      countProducts() {
                    // код для работы с данными от локального источника
                    // return this.filteredProducts.length;
        // код для работы с сервером
        return this.productsData ? this.productsData.pagination.total : 0;
      },

    },
    methods: {
      // метод для загрузки данных
      loadProducts(){
        // axios.get(`https://vue-study.skillbox.cc/api/products?page=${this.page}&limit=${this.productsPerPage}`)
        // подключаем прелоадер
        this.productsLoadingFailed = false;
        this.productsLoading = true;
        clearTimeout(this.loadProductsTimer);
        this.loadProductsTimer = setTimeout (() => {
          axios.get(API_BASE_URL + `/api/products`, {
          params: {
            page: this.page,
            limit: this.productsPerPage,
            categoryId: this.filterCategoryId,
            minPrice: this.filterPriceFrom,
            maxPrice: this.filterPriceTo,
            colorId: this.filterColorValue,
          }
        })
        // если ответ 200 то записываем ео в свойство состояния productsData
        .then(response => this.productsData = response.data)
        // ловим ошибку
        .catch(() => this.productsLoadingFailed = true)
        // отключаем прелоадер
        .then(() => this.productsLoading = false)
        }, 1000);
      },
    },
    
    watch: {
      // наблюдаем за изенением страницы
      page() {
        this.loadProducts();
      },
      filterPriceFrom(){
        this.loadProducts();
      },
      filterPriceTo(){
        this.loadProducts();
      },
      filterCategoryId(){
        this.loadProducts();
      },
      filterColorValue(){
        this.loadProducts();
      }
    },
    // вызываем метод в хук жизненного цикла
    created() {
      this.loadProducts();
    }
}
</script>

